<template>
  <div class="all">
      <div class="row justify-content-center pt-5">
          <div class="col-1"></div>
          <div class="col-10">
              <h1>{{examfont}}</h1>
          </div>
          <div class="col-1"></div>
      </div>
      <div class="row justify-content-center mt-5" 
        v-for="(item,index) in choseengarr" :key="index">
          <div class="col-2"></div>
          <div class="col-8">
              <button  type="button" class="btn btn-info" @click="judge(item)">{{item}}</button>
          </div>
          <div class="col-2"></div>
      </div>
      <div v-if="error" class="row justify-content-center pt-5">
          <div class="col-12">
              <h2>錯了</h2>
          </div>
      </div>
  </div>
</template>
<script>
/*eslint-disable*/
import { showapi,addapi } from "../api/api.js";
export default {
  name: 'pschool',
  data:()=>({
      classdetail:[
          {"eng":"A219","class":"AutoCAD 2D機構入門"},
          {"eng":"A220","class":"AutoCAD 2D室內入門"},
          {"eng":"A320","class":"AutoCAD 3D機構入門"},
          {"eng":"A321","class":"AutoCAD 3D室內入門"},
          {"eng":"AP18","class":"Photoshop CC影像設計"},
          {"eng":"AR17","class":"Illustrator CC插畫設計"},
          {"eng":"ARL2","class":"Line動態貼圖設計"},
          {"eng":"AT7","class":"數位插畫設計養成"},
          {"eng":"AX18","class":"3ds Max室內入門"},
          {"eng":"AX20","class":"3ds Max遊戲入門"},
          {"eng":"DB34","class":"資料庫概論"},
          {"eng":"EA34","class":"基本圖學-機構設計"},
          {"eng":"EA35","class":"基本圖學-建築室內設計"},
          {"eng":"EAP19","class":"Photoshop CC網頁版型設計"},
          {"eng":"EID20","class":"DreamWeaver CC創意網頁設計"},
          {"eng":"EWO12","class":"Outlook數位助理"},
          {"eng":"HA4","class":"硬體組裝與維修"},
          {"eng":"LC10","class":"C語言程式設計"},
          {"eng":"LC11","class":"C++程式設計"},
          {"eng":"LJ14","class":"Java基礎程式設計"},
          {"eng":"LS7","class":"ASP.NET程式設計"},
          {"eng":"MA8","class":"After Effects視訊特效"},
          {"eng":"MP9","class":"Premiere CC多媒體視訊剪輯"},
          {"eng":"R3SS2","class":"3ds Max室內設計"},
          {"eng":"RACS4","class":"Auto CAD室內設計"},
          {"eng":"RACS5","class":"室內設計實務班"},
          {"eng":"RACU2","class":"Auto CAD機構設計"},
          {"eng":"RADP5","class":"智能操控行動裝置開發"},
          {"eng":"RC14","class":"Cisco CCNA網路工程師"},
          {"eng":"RC15","class":"CCNP-Cisco企業網路核心技術(主)"},
          {"eng":"RC16","class":"CCNP-Cisco企業Route技術服務(選)"},
          {"eng":"RC17","class":"CCNP Security企業安全核心技術(主)"},
          {"eng":"RC18","class":"CCNP Security資訊安全性存取技術"},
          {"eng":"RD15","class":"響應式網頁前端設計"},
          {"eng":"RIOT1","class":"AIOT智慧物聯網程式應用"},
          {"eng":"RM17","class":".NET程式設計國際認證班"},
          {"eng":"RM18","class":"HTML5網路應用程式開發班"},
          {"eng":"RMSN7","class":"混合雲建置管理認證"},
          {"eng":"RMSN8","class":"雲端資訊安全認證"},
          {"eng":"ROJP3","class":"Java企業產學就業班"},
          {"eng":"ROJP4","class":"Java OCP JP程式設計師認證班"},
          {"eng":"ROJP5","class":"Java Web OCE JWCD元件系統開發認證"},
          {"eng":"ROJP6","class":"Java企業框架實務班"},
          {"eng":"RPE1","class":"ProIE Creo產品設計認證班"},
          {"eng":"RPK2","class":"平面設計師實務養成"},
          {"eng":"RPTG2","class":"遊戲原畫設計師"},
          {"eng":"RSSK3","class":"sketch UP&Vray室內設計與擬真渲染"},
          {"eng":"RSQD8","class":"SQL Server資料庫基礎認證班"},
          {"eng":"RSQD9","class":"SQL Server資料庫管理認證班"},
          {"eng":"RSQD10","class":"SQL Server資料開發庫認證班"},
          {"eng":"RZ2","class":"ZBrush 3D動畫造型設計"},
          {"eng":"Uo2","class":"TCP/IP原理與應用"},
          {"eng":"VC7","class":"C#.NET程式設計"},
          {"eng":"WE17","class":"Excel試算表製作"},
          {"eng":"WNU","class":"Hyper-V系統建置實務"},
          {"eng":"WNV","class":"Server2019系統建置"},
          {"eng":"WP11","class":"Power Point多媒體簡報製作"},
          {"eng":"WW17","class":"Word文書處理"},
      ],
      examfont:null,
      chosearr:null,
      choseengarr:[],
      error:false,
      apidata:null,
      nowdata:null
  }),
  mounted(){
        showapi({
        })
        .then(res=> {
            this.apidata = res.data.data
            this.next()
        })
        .catch(err=> {
            console.log(err);
            this.next()
        })
  },
  methods:{
       next(){
            this.nowdata  = this.apidata
            if(this.apidata == null){
                this.nowdata = this.classdetail
            }
            this.trueans = null
            this.chosearr = []

            for(var i  = 0;i<4;i++){
                var temp
                var status = true
                while(status){
                    temp = Math.floor(Math.random() * this.nowdata.length)
                    if(this.chosearr.indexOf(temp) == -1){
                        this.chosearr.push(temp)
                        status = false
                    }else{
                        status = true
                    }
                }
            }

            this.trueans = this.chosearr[Math.floor(Math.random() * 4)]

            for(var i in this.chosearr){
                this.choseengarr[i] = this.nowdata[this.chosearr[i]]['eng'];
            }

            this.examfont = this.nowdata[this.trueans]['class'];
       },
       judge(eng){
           if(eng == this.nowdata[this.trueans]['eng']){
                this.error = false
                this.next()
           }else{
                this.error = true
           }
       },
       add(){
           addapi({
                id:"",
                eng: 'A321',
                class: 'AutoCAD 3D室內入門'
            })
            .then(res=> {
                console.log(res)
            })
            .catch(err=> {
                console.log(err);
            })
       }
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
    .all{
        position: relative;
        background-color: rgb(245, 234, 219);
        height: 100vh;
    }
    h2{
        color: red;
    }
</style>
