<template>
  <div class="all">
    <div class="row pt-5">
        <h1>後台</h1>
    </div>
    <div class="row mt-2">
        <div class="col-2">
          <h4>代號:</h4>
        </div>
        <div class="col-2">
          <input type="text" v-model="eng">
        </div>
        <div class="col-2">
          <h4>中文名稱:</h4>
        </div>
        <div class="col-2">
          <input type="text" v-model="classname">
        </div>
        <div class="col-4">
            <button  type="button" class="btn btn-info" @click="add()">新增</button>
        </div>
    </div>
    <div class="row" v-if="adderror">
        <h3>新增格式錯誤</h3>
    </div>
    <div class="row mt-2 justify-content-center">
      <div class="col-2"></div>
      <div class="col-8">
        <vue-good-table :columns="columns" :rows="rows"
        :search-options="{
        enabled: true,
        trigger: 'enter'
        }"
        :pagination-options="{
              enabled: true,
              mode: 'pages'
            }"
        >
        <template slot="table-row" slot-scope="props">
          <div class="row" v-if="props.column.field == 'del'">
            <div class="col-4">
              <button v-b-modal.modal-center class="btn btn-warning size" @click="madeid(props)">改</button>
            </div>
            <div class="col-2"></div>
            <div class="col-4">
              <button class="btn btn-danger size" @click="del(props)">刪</button>
            </div>
            <div class="col-1"></div>
          </div>
        </template>
        </vue-good-table>
      </div>
      <div class="col-2"></div>
    </div>
    <!-- Modal -->
    <b-modal id="modal-center" centered title="BootstrapVue" ok-only ok-variant="secondary" ok-title="Cancel">
      <div class="row mt-2">
          <div class="col-4">
            <h6>代號:</h6>
          </div>
          <div class="col-8">
            <input type="text" v-model="upeng">
          </div>
      </div>
      <div class="row mt-2">
          <div class="col-4">
            <h6>中文名稱:</h6>
          </div>
          <div class="col-8">
            <input type="text" v-model="upclassname">
          </div>
      </div>
      <div class="row mt-2">
          <div class="col-2"></div>
          <div class="col-8">
              <button type="button" class="btn btn-info size" @click="made()">修改</button>
          </div>
          <div class="col-2"></div>
      </div>
      <div class="row text-center" v-if="updataerror">
        <h3>修改格式錯誤</h3>
      </div>
    </b-modal>
  </div>
</template>
<script>
/*eslint-disable*/
import { showapi,addapi,delapi,updateapi } from "../api/api.js";
export default {
  name: 'server',
  data:()=>({
      columns: [
        {
          label: 'id',
          field: 'id',
          width: '10%',
        },
        {
          label: 'eng',
          field: 'eng',
          width: '30%',
        },
        {
          label: 'class',
          field: 'class',
          width: '40%',
        },
        {
          label: '  ',
          field: 'del',
          width: '20%',
        },
      ],
      rows: [
        
      ],
      classname:"",
      eng:"",
      apidata:[],
      upclassname:"",
      upeng:"",
      upid:null,
      adderror:false,
      updataerror:false
  }),
  computed:{
  
    },
  mounted(){
      this.show()
  },
  methods:{
      add(){
           if((this.classname != "")&&(this.eng != "")){
             this.adderror = false
             addapi({
                  id:"",
                  eng: this.eng,
                  class: this.classname
              })
              .then(res=> {
                  console.log(res)
                  this.classname = ""
                  this.eng = ""
                  this.show()
              })
              .catch(err=> {
                  console.log(err);
              })
           }else{
             this.adderror = true
           }
      },
      show(){
        const that = this
        showapi({
        })
        .then(res=> {
            console.log(res)
            that.apidata = res.data.data
            console.log(that.apidata);
            that.rows = that.apidata
        })
        .catch(err=> {
            console.log(err);
        })
      },
      del(detail){
        console.log(detail.row.id)
        delapi({
                id:detail.row.id,
            })
            .then(res=> {
                console.log(res)
                this.show()
            })
            .catch(err=> {
                console.log(err);
            })
      },
      madeid(detail){
        this.upid = detail.row.id
      },
      made(){
        const that = this
        if((this.upclassname != "")&&(this.upeng != "")){
             this.updataerror = false
             updateapi({
                  id:that.upid,
                  eng: that.upeng,
                  class: that.upclassname
              })
              .then(res=> {
                  console.log(res)
                  that.upclassname = ""
                  that.upeng = ""
                  that.show()
                  $(".close").click()
              })
              .catch(err=> {
                  console.log(err);
              })
           }else{
             this.updataerror = true
           }
      }
  },
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.size{
  width: 100%;
}
</style>
